.theme-dark{--bg:radial-gradient(circle at top left,#8a2be2 0%,#4b0082 30%,#050013 70%,#000000 100%);--card-bg:rgba(12,3,40,.72);--accent:#c084ff;--accent-soft:rgba(192,132,255,.18);--text-primary:#fdfbff;--text-secondary:rgba(232,223,255,.78)}
.theme-light{--bg:linear-gradient(135deg,#fdfbff 0%,#e9ddff 40%,#f7efff 100%);--card-bg:rgba(255,255,255,.86);--accent:#7c3aed;--accent-soft:rgba(124,58,237,.12);--text-primary:#1f102b;--text-secondary:rgba(55,30,90,.78)}
.app{width:100vw;height:100vh;display:flex;align-items:center;justify-content:center;background:var(--bg);color:var(--text-primary);font-family:-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,sans-serif;padding:32px}
.card{width:100%;max-width:960px;height:100%;max-height:560px;border-radius:32px;background:var(--card-bg);box-shadow:0 24px 80px rgba(0,0,0,.55),0 0 40px rgba(168,85,247,.56);border:1px solid rgba(255,255,255,.14);backdrop-filter:blur(26px);-webkit-backdrop-filter:blur(26px);padding:32px;display:flex;flex-direction:column;position:relative;overflow:hidden}
.grid{flex:1;display:grid;grid-template-columns:minmax(0,2.1fr) minmax(0,1.2fr);gap:24px;position:relative;z-index:1}
.status-list{list-style:none;padding:0;margin:0;font-size:14px;display:grid;gap:6px}
.accent-btn{margin-top:24px;align-self:flex-start;padding:14px 32px;border-radius:999px;border:0;font-size:15px;font-weight:600;letter-spacing:.6px;text-transform:uppercase;background:linear-gradient(120deg,#a855f7,#ec4899,#f97316);color:#0b0214;cursor:pointer;box-shadow:0 12px 32px rgba(168,85,247,.65),0 0 24px rgba(236,72,153,.85);transform:translateY(0);transition:transform .16s ease-out,box-shadow .16s ease-out,filter .16s ease-out;filter:drop-shadow(0 0 12px rgba(249,115,22,.5))}
.accent-btn:hover{transform:translateY(-2px);box-shadow:0 18px 42px rgba(168,85,247,.9),0 0 32px rgba(236,72,153,1)}
